import Vue from 'vue';
import Vuex from 'vuex';
import mutations from './mutation';
import actions from './action'

Vue.use(Vuex);

const path_title_array = [
    ['/', 'Vue.js'],
    ['/publish', ''],
    ['/messages', ''],
    ['/person', ''],
    ['/content', ''],
    ['/login', ''],
    ['/user', '']
];
const state = {
    svrUrl: 'https://cnodejs.org/api/v1/',
    titleMap: new Map(path_title_array),
    tabValue: ''
}

export default new Vuex.Store({
    state,
    mutations,
    actions
})